import { Row, Col } from "react-bootstrap"

const PreLoginPage = () => {
    const LoginButtonPressed = async () => {
        const data = '?client_id=68084&redirect_uri=https://simonuks221.github.io/StravaKudosTracker&response_type=code&scope=activity:read'
        const response = await fetch('https://www.strava.com/oauth/authorize' + data)
        console.log('sent')
        console.log('response: ', response)
    }

    return (
        <div className = 'pre-login'>
            <Row>
                <h1>Welcome to the kudos tracker</h1>
            </Row>
            <Row>
                <button onClick = {LoginButtonPressed}>Login to strava</button>
            </Row>
        </div>
    )
}

export default PreLoginPage
